/* Reset default list styles */
ul {
  list-style-type: none;
  padding: 0;
}

/* Style for the to-do list container */
.todo-list {
  width: 300px;
  margin: 0 auto;
  font-family: Arial, sans-serif;
}

/* Style for individual to-do items */
.todo-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border: 1px solid #ccc;
  padding: 10px;
  margin: 5px 0;
  background-color: #fff;
}

/* Style for completed to-do items */
.todo-item.completed {
  text-decoration: line-through;
  color: #777;
}
.completed {
  text-decoration: line-through;
  color: #777;
}
/* Style for the to-do item checkbox */
.todo-item .checkbox {
  margin-right: 10px;
}

/* Style for the delete button */
.delete-button {
  background-color: #ff4136;
  color: #fff;
  border: none;
  padding: 5px 10px;
  cursor: pointer;
  text-decoration: none;
}

/* Hover effect for delete button */
.delete-button:hover {
  background-color: #d30f0f;
}
